	module.exports = {"data":{"markdownRemark":{"html":"<p>Magus-  After years of digging the fungal abomination has finally made\nheadway. Progress would have been quicker, but the wretched sun lovers and\nfilthy uruk-hai couldnt seem to stop throwing themselves at us in an attempt\nto steal the abominations flail. So, it is upon a heap of crushed and broken\nbodies that we found what we were looking for. Your research into the old\ntexts was correct. There was once a tall tower that was built upon a site of\ndark power. The hobbits that lived there were slowly corrupted until they\nbarely remembered their true selves. The dark power ate away at them, little\nby little, such that they didnt even notice the change happening within them.\nOver time, the tower sank deeper and deeper into the ground until it finally\nfell. We ventured into the fallen tower and captured a hobbit, or naerling as\nit called itself. I cant be sure if the treasure youre seeking is within, but\nthere is definitely treasure to be found.</p>\n<p>-Lazora</p>\n<p>OOC: Design and original build by Wart/Traw. Huge thanks to\nPat and Neil for lots of extra work that went into the zone.</p>","frontmatter":{"title":"News 1669","date":"April 10, 2018","author":"Lowtheim","desc":"4-10-2018 World Update"}}},"pathContext":{"path":"/news/1669"}}

